@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@700&display=swap);
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1.7;
}
ol,
ul {
  list-style: none;
}
a {
  color: inherit;
  text-decoration: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

body{background-color:#f5f5f5}.dark{background-color:#121212 !important;color:#fff !important}h2,h3,h4,h5,h6{color:#0f4c81}h1,h2,h3,h4,h5,h6,strong,em{font-family:"Roboto Slab", serif}header,main,footer{width:100%;box-sizing:border-box;padding-left:14px;padding-right:14px}@media only screen and (min-width: 768px){header,main,footer{padding-left:70px;padding-right:70px}}

body {
  position: relative;
}

.home {
  height: 100vh;
}

.home h1 {
  font-weight: 500;
  font-size: 36px;
}

.home p {
  font-weight: 300;
  size: 24px;
}

.home {
  display: grid;
  grid-gap: 16px;
  gap: 16px;
}

@media screen and (min-width: 1023px) {
  .home {
    grid-template-columns: 1fr 1fr;
  }

  .apresentation-container .apresentation {
    margin: 0;
  }
}

.apresentation-container,
.img-container {
  display: flex;
  align-items: center;
}

.apresentation-container .apresentation {
  height: auto;
  padding: 50px 0;
  max-width: 400px;
}

.img-container img {
  max-width: 1000px;
  width: 100%;
}

.apresentation-container .icons-group {
  margin-top: 24px;
  max-width: 130px;
  display: flex;
  justify-content: space-between;
}



header {
  font-size: 16px;
  padding-top: 16px;
  padding-bottom: 16px;
  font-weight: 500;
}

nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.menu-group-links {
  text-align: center;
}

.menu-group-links .menu-link {
  margin-left: 32px;
}

.menu-link {
  transition: all ease 0.5s;
  opacity: 0.9;
}

.menu-link.active,
.menu-link:hover {
  color: #0f4c81;
  opacity: 1;
}
footer {
  width: 100%;
  padding-top: 32px;
  padding-bottom: 32px;
  display: flex;
  justify-content: center;
  align-content: center;
}

.icons-group {
  width: 150px;
  display: flex;
  justify-content: space-between;
}

.project {
  position: relative;
  box-sizing: border-box;
  padding: 16px 24px;
  background-color: #fff;
  height: 350px;
  width: 100%;
  border-radius: 16px;
  transition: all 0.5s linear;
  border: 2px solid transparent;
}

.project:hover {
  border: 2px solid #0f4c81;
}

@media screen and (max-width: 600px) {
  .project {
    height: auto;
    padding-bottom: 64px;
  }
}

.project p {
  font-weight: 400;
  font-size: 16px;
  color: #212121;
}

.project h3 {
  font-size: 24px;
  margin-bottom: 8px;
  font-weight: 500;
}

.projects-main h1 {
  margin-top: 64px;
  color: #0f4c81;
}

.projects-main h1 + p {
  font-weight: 300;
  margin-bottom: 40px;
}

.projects-main {
  padding-bottom: 64px;
}

.projects-container {
  box-sizing: border-box;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-row-gap: 32px;
  row-gap: 32px;
  grid-column-gap: 30px;
  -webkit-column-gap: 30px;
          column-gap: 30px;
  justify-content: start;
}

@media screen and (min-width: 600px) {
  .projects-container {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (min-width: 1024px) {
  .projects-container {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media screen and (min-width: 1300px) {
  .projects-container {
    grid-template-columns: repeat(4, 1fr);
  }
}

@media screen and (min-width: 1600px) {
  .projects-container {
    grid-template-columns: repeat(5, 1fr);
  }
}

.skill-content {
  display: block;
}

.skill {
  background-color: #fff;
  border-radius: 8px;

  height: 200px;
  width: 200px;

  margin: 0px 8px 8px 0px;

  display: flex;
  justify-content: center;
  align-content: center;
  flex-flow: column;

  text-align: center;

  transition: all 0.3s linear;
}

.skill img {
  max-height: 100px;
  max-width: 100px;
  margin-bottom: 8px;
}

.skill legend {
  color: #454545;
  font-weight: 600;
}

@media screen and (max-width: 850px) {
  .skill {
    height: 166px;
    width: 166px;
  }

  .skill img {
    max-width: 80px;
    max-height: 100px;
  }
}

@media screen and (max-width: 768px) {
  .skill {
    height: 110px;
    width: 110px;
  }

  .skill img {
    max-width: 50px;
    max-height: 50px;
  }
}

@media screen and (max-width: 425px) {
  .skill {
    height: 120px;
    width: 120px;
    flex-grow: 1;
  }
}

.experience {
  width: 100%;
  min-height: 300px;

  padding: 40px 16px;
  margin-top: 24px;
  margin-bottom: 40px;

  background-color: #fff;
  border-radius: 16px;

  display: flex;
  flex-direction: row;
  align-content: center;
  flex-wrap: wrap;
}

.experience .details-container h4 {
  font-size: 24px;
  margin-bottom: 16px;
  color: #212121;
}

.experience .details-container p {
  font-size: 16px;
}

.experience .details-container p:last-child {
  margin-top: 16px;
  font-size: 12;
  font-style: italic;
  font-weight: 300;
}

.experience .img-container {
  width: 30%;
  min-width: 250px;
  display: flex;
  justify-content: center;
}

.experience .details-container {
  width: 70%;
}

@media screen and (max-width: 1014px){
  .experience .img-container {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-bottom: 64px;
  }
  
  .experience .details-container {
    width: 100%;
  }
}
.about-main h2 {
  font-size: 32px;
}

.about-main section{
  margin-bottom: 72px;
}

.profile-container {
  display: flex;
  justify-content: center;
  align-content: center;
  padding-top: 64px;
  padding-bottom: 64px;
}

.profile-container .profile {
  max-width: 400px;
  text-align: center;
}

.profile-container .profile .profile-img {
  width: 206px;
  height: 206px;
  border-radius: 100%;
  margin-bottom: 16px;
}

.profile-container .profile p {
  margin-bottom: 32px;
}

.profile-container .profile .icons-group {
  margin: 0 auto;
}

.profile-container .profile .icons-group a {
  margin-left: 8px;
  margin-right: 8px;
}

.skills-container .skills {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;  
}

.skills-container p {
  margin-bottom: 24px;
  font-weight: 300;
}

.profile-container,
.skills-container {
  margin-bottom: 32px;
}

